import React, { useState, useEffect } from 'react';
import axios from 'axios';
import '../styles/AuthModal.css';
import { URLS } from '../urls';

const api = axios.create({
  baseURL: URLS.API_BASE_URL,
  withCredentials: true
});

function AuthModal({ onLoginSuccess }) {
  const [email, setEmail] = useState('');
  const [username, setUsername] = useState('');
  const [nickname, setNickname] = useState('');
  const [password, setPassword] = useState('');
  const [confirmPassword, setConfirmPassword] = useState('');
  const [errors, setErrors] = useState({});
  const [duplicates, setDuplicates] = useState({});
  const [isLoading, setIsLoading] = useState(false);
  const [isLoginMode, setIsLoginMode] = useState(true);

  useEffect(() => {
    if (!isLoginMode && username.length >= 4) {
      checkDuplicate('username');
    }
  }, [username, isLoginMode]);

  useEffect(() => {
    if (!isLoginMode && email.length > 0) {
      checkDuplicate('email');
    }
  }, [email, isLoginMode]);

  useEffect(() => {
    if (!isLoginMode && nickname.length >= 2) {
      checkDuplicate('nickname');
    }
  }, [nickname, isLoginMode]);

  const validateForm = () => {
    const newErrors = {};
    
    if (!isLoginMode) {
      if (!email || !/\S+@\S+\.\S+/.test(email)) {
        newErrors.email = '   .';
      }
      if (!nickname || nickname.length < 2) {
        newErrors.nickname = ' 2  .';
      }
      if (password !== confirmPassword) {
        newErrors.confirmPassword = '  .';
      }
    }

    if (!username || username.length < 4) {
      newErrors.username = ' 4  .';
    }
    if (!password || password.length < 8) {
      newErrors.password = ' 8  .';
    }

    setErrors(newErrors);
    return Object.keys(newErrors).length === 0;
  };

  const checkDuplicate = async (field) => {
    if (!isLoginMode && (field === 'email' || field === 'username' || field === 'nickname')) {
      try {
        const response = await api.post(URLS.CHECK_DUPLICATE, { [field]: eval(field) });
        if (response.data.isDuplicate) {
          setDuplicates(prev => ({ ...prev, [field]: ` ${field === 'email' ? '' : field === 'username' ? '' : ''}   .` }));
        } else {
          setDuplicates(prev => ({ ...prev, [field]: null }));
        }
      } catch (error) {
        console.error('Error checking duplicate:', error);
        setErrors(prev => ({ ...prev, api: '  .   .' }));
      }
    }
  };

  const handleSubmit = async (e) => {
    e.preventDefault();
    if (validateForm() && !Object.values(duplicates).some(Boolean)) {
      setIsLoading(true);
      try {
        let response;
        if (isLoginMode) {
          response = await api.post(URLS.LOGIN, { username, password });
          if (response.data && response.data.user_id) {
            onLoginSuccess({
              userId: response.data.user_id,
              username: username
            });
          } else {
            throw new Error('Invalid server response');
          }
        } else {
          response = await api.post(URLS.REGISTER, { email, username, nickname, password });
          if (response.data && response.data.user_id) {
            onLoginSuccess({
              userId: response.data.user_id,
              username: username
            });
          }
        }
        setIsLoading(false);
      } catch (error) {
        console.error('Error:', error.response ? error.response.data : error.message);
        setErrors({ api: error.response ? error.response.data.detail : '  .' });
        setIsLoading(false);
      }
    }
  };

  const toggleMode = () => {
    setIsLoginMode(!isLoginMode);
    setErrors({});
    setDuplicates({});
    setEmail('');
    setUsername('');
    setNickname('');
    setPassword('');
    setConfirmPassword('');
  };

  return (
    <div className="auth-modal">
      <h2>{isLoginMode ? '' : ''}</h2>
      <form onSubmit={handleSubmit}>
        {!isLoginMode && (
          <>
            <input
              type="email"
              placeholder=""
              value={email}
              onChange={(e) => setEmail(e.target.value)}
              required
            />
            {errors.email && <p className="error">{errors.email}</p>}
            {duplicates.email && <p className="duplicate-error">{duplicates.email}</p>}
            <input
              type="text"
              placeholder=""
              value={username}
              onChange={(e) => setUsername(e.target.value)}
              required
            />
            {errors.username && <p className="error">{errors.username}</p>}
            {duplicates.username && <p className="duplicate-error">{duplicates.username}</p>}
            <input
              type="text"
              placeholder=""
              value={nickname}
              onChange={(e) => setNickname(e.target.value)}
              required
            />
            {errors.nickname && <p className="error">{errors.nickname}</p>}
            {duplicates.nickname && <p className="duplicate-error">{duplicates.nickname}</p>}
          </>
        )}
        {isLoginMode && (
          <input
            type="text"
            placeholder=""
            value={username}
            onChange={(e) => setUsername(e.target.value)}
            required
          />
        )}
        <input
          type="password"
          placeholder=""
          value={password}
          onChange={(e) => setPassword(e.target.value)}
          required
        />
        {errors.password && <p className="error">{errors.password}</p>}
        {!isLoginMode && (
          <>
            <input
              type="password"
              placeholder=" "
              value={confirmPassword}
              onChange={(e) => setConfirmPassword(e.target.value)}
              required
            />
            {errors.confirmPassword && <p className="error">{errors.confirmPassword}</p>}
          </>
        )}
        {errors.api && <p className="error">{errors.api}</p>}
        <button type="submit" disabled={isLoading || Object.values(duplicates).some(Boolean)}>
          {isLoading ? ' ...' : (isLoginMode ? '' : '')}
        </button>
      </form>
      <button onClick={toggleMode} className="switch-auth-type">
        {isLoginMode ? '' : ''}
      </button>
    </div>
  );
}

export default AuthModal;